
.tab {
  display: -ms-flexbox;
  display: flex;
  height: 40px;
  line-height: 40px;
  position: relative;
}
.tab:after {
  position: absolute;
  display: block;
  left: 0;
  bottom: 0;
  width: 100%;
  border-top: 1px solid rgba(7,17,27,0.1);
  content: '';
}
.tab .tab-item {
  color: #000;
  -ms-flex: 1;
      flex: 1;
  text-align: center;
  font-size: 14px;
  display: block;
}
.tab .tab-item.router-link-active {
  color: #f01414;
}


