
.header {
  position: relative;
  width: 100%;
  font-size: 0;
  background: rgba(0,0,0,0.3);
  color: #fff;
}
.header .content-wrapper {
  padding: 24px 12px 18px 24px;
  position: relative;
}
.header .content-wrapper .avatar {
  display: inline-block;
  vertical-align: top;
}
.header .content-wrapper .content {
  margin-left: 6px;
  display: inline-block;
}
.header .content-wrapper .content .title {
  margin-bottom: 6px;
}
.header .content-wrapper .content .title .brand {
  display: inline-block;
  background-image: url("brand@2x.png");
  background-repeat: no-repeat;
  width: 30px;
  height: 18px;
  background-size: 30px 18px;
  background-repeat: no-repeat;
  margin-right: 6px;
  vertical-align: top;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .content-wrapper .content .title .brand {
    background-image: url("brand@3x.png");
    background-repeat: no-repeat;
}
}
.header .content-wrapper .content .title .name {
  display: inline-block;
  font-size: 18px;
  line-height: 18px;
  font-weight: 700;
}
.header .content-wrapper .content .decription {
  font-size: 12px;
  line-height: 12px;
  margin: 5px 0;
}
.header .content-wrapper .content .support {
  margin: 5px 0;
}
.header .content-wrapper .content .support .icon {
  display: inline-block;
  background-size: 12px 12px;
  width: 12px;
  height: 12px;
  background-repeat: no-repeat;
  vertical-align: top;
  margin-right: 4px;
}
.header .content-wrapper .content .support .text {
  display: inline-block;
  font-size: 12px;
  line-height: 12px;
}
.header .content-wrapper .supports {
  position: absolute;
  right: 12px;
  bottom: 12px;
  height: 24px;
  padding: 0 8px;
  line-height: 24px;
  border-radius: 4px;
  background: rgba(0,0,0,0.2);
}
.header .content-wrapper .supports .count {
  font-size: 10px;
  display: inline-block;
}
.header .content-wrapper .supports .icon-keyboard_arrow_right {
  display: inline-block;
  font-size: 10px;
  text-align: center;
}
.header .bulletin-wrapper {
  position: relative;
  height: 28px;
  line-height: 28px;
  padding: 0 22px 0 12px;
  background: rgba(7,17,27,0.2);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.header .bulletin-wrapper .bulletin-title {
  display: inline-block;
  background-image: url("bulletin@2x.png");
  background-repeat: no-repeat;
  background-size: 22px 12px;
  width: 22px;
  height: 12px;
  margin-top: 8px;
  vertical-align: top;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .bulletin-wrapper .bulletin-title {
    background-image: url("bulletin@3x.png");
    background-repeat: no-repeat;
}
}
.header .bulletin-wrapper .bulletin-text {
  display: inline-block;
  font-size: 10px;
  line-height: 28px;
  margin: 0 4px;
}
.header .bulletin-wrapper .icon-keyboard_arrow_right {
  display: inline-block;
  font-size: 10px;
  text-align: center;
  position: absolute;
  right: 12px;
  top: 8px;
}
.header .background {
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  z-index: -1;
  height: 100%;
  -webkit-filter: blur(10px);
          filter: blur(10px);
}
.header .detail {
  position: fixed;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  background: rgba(0,0,0,0.7);
  z-index: 100;
  overflow: auto;
  opacity: 1;
}
.header .detail.fade-enter-active,
.header .detail.fade-leave-active {
  transition: all 0.5s;
}
.header .detail.fade-enter,
.header .detail.fade-leave-to {
  opacity: 0;
  background: rgba(7,17,27,0);
}
.header .detail .detail-wrapper {
  width: 80%;
  min-height: 100%;
  margin: 64px auto 0 auto;
  padding-bottom: 64px;
}
.header .detail .detail-wrapper .name {
  font-size: 20px;
  margin: 50px auto;
  text-align: center;
}
.header .detail .detail-wrapper .star-wrapper {
  width: 100%;
  text-align: center;
}
.header .detail .detail-wrapper .title {
  display: -ms-flexbox;
  display: flex;
  margin: 30px 0;
}
.header .detail .detail-wrapper .title .line {
  -ms-flex: 1;
      flex: 1;
  border-bottom: 1px solid #ccc;
}
.header .detail .detail-wrapper .title .text {
  -ms-flex: 0 0 80px;
      flex: 0 0 80px;
  font-size: 20px;
  padding: 0 20px;
}
.header .detail .detail-wrapper .supports .suports-item .icon {
  display: inline-block;
  width: 16px;
  height: 16px;
  background-size: 16px 16px;
  background-repeat: no-repeat;
  margin-right: 4px;
}
.header .detail .detail-wrapper .supports .suports-item .text {
  display: inline-block;
  font-size: 14px;
  line-height: 14px;
}
.header .detail .detail-wrapper .bulletin {
  font-size: 14px;
  line-height: 28px;
}
.header .detail .detail-close {
  position: relative;
  width: 32px;
  height: 32px;
  margin: -64px auto 0 auto;
  clear: both;
  font-size: 32px;
}


